.cardItem {
  min-width: 380px;
  max-width: 380px;
  position: relative;
  margin: 10px;
  padding-top: 20px;
  padding-bottom: 36px;
  list-style: none;
  background: linear-gradient(
    114.99deg,
    #471ca9 -0.99%,
    #5736a3 54.28%,
    #4b2a99 78.99%
  );
  box-shadow: -2.5777px 6.87386px 20.6216px rgba(0, 0, 0, 0.23);
  border-radius: 20px;
  flex-basis: calc(100% / 3);
}

.logo {
  margin-left: 20px;
}
.picture {
  position: absolute;
  width: 308px;
  height: 168px;
  top: 28px;
  left: 50%;
  transform: translateX(-50%);
}
.line {
  margin-top: 172px;
  height: 15px;
  box-sizing: border-box;
}

.avatar {
  position: absolute;
  width: 80px;
  height: 80px;
  top: 178px;
  left: 50%;
  transform: translateX(-50%);
  border-radius: 100%;
}

.textWrapp {
  width: 80%;
  margin: 0 auto;
  margin-top: 55px;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.tweets {
  margin: 0;
  padding: 0;
  font-weight: 500;
  font-size: 20px;
  line-height: 1.2;
  text-transform: uppercase;
  color: #ebd8ff;
}
.followers {
  margin: 0;
  padding: 0;
  margin-top: 16px;
  font-weight: 500;
  font-size: 20px;
  line-height: 1.2;
  text-transform: uppercase;
  color: #ebd8ff;
}
.followingBtn {
  display: block;
  width: 196px;
  height: 50px;
  margin: 0 auto;
  margin-top: 26px;
  background: #ebd8ff;
  box-shadow: 0px 3.43693px 3.43693px rgba(0, 0, 0, 0.25);
  border-radius: 10.3108px;
  border: none;
  font-weight: 600;
  font-size: 18px;
  line-height: 1.22;
  text-transform: uppercase;
  color: #373737;
}
.followingBtn:hover {
  background: #b379ee;
}

.followingBtn.active {
  background: #5cd3a8;
  box-shadow: 0px 3.43693px 3.43693px rgba(0, 0, 0, 0.25);
  border-radius: 10.3108px;
}

.isLoading {
  margin-top: 40px;
  text-align: center;
  font-weight: 600;
  font-size: 18px;
  line-height: 22 / 18;
  text-transform: uppercase;
  color: #373737;
}
